import * as React from "react"
import { navigate } from "gatsby"
import { Form, Button, Card } from 'react-bootstrap'
import { handleLogin, isLoggedIn } from "../../services/auth"
import background from '../../images/login_background.jpg'

class Login extends React.Component {
  state = {
    email: ``,
    password: ``,
    error: false
  }

  handleUpdate = event => {
    this.setState({
      [event.target.name]: event.target.value,
    })
  }

  handleSubmit = async event => {
    event.preventDefault()
    console.log(this.state);
    await handleLogin(this.state);
    navigate(`/admin`)
  }

  render() {
    if (isLoggedIn()) {
      navigate(`/app/admin`)
      // use here to redirect to whatever u want
    }
    const backgroundStyle = { // CSS for div
      opacity: '0.8',
      backgroundImage: `url(${background})`,
      backgroundImageSize: '100%',
      backgroundRepeat: 'repeat',
      backgroundSize: '100%',
      position: 'absolute',
      width: '100%',
      height: '100%',
      top: '0px',
      left: '0px',
      zIndex: '100',
    };
    const cardStyle = { // CSS for card div
      width: '30%',
      position: 'absolute',
      top: 'calc(50% - 50px)',
      left: 'calc(50% - 15.10%)'
    };

    return (
      <>
        <div style={backgroundStyle}>
          <main>
            <div style={cardStyle}>
              <Card style={{ boxShadow: '7px 5px 5px black' }}>
                <Card.Body>
                  <h2 className='text-center mb-4'>Log In</h2>
                  <Form method="post"
                    onSubmit={event => {
                      this.handleSubmit(event)

                    }}>
                    <Form.Group id="email">
                      <Form.Label>Email</Form.Label>
                      <Form.Control name="email" onChange={this.handleUpdate} required />
                    </Form.Group>
                    <Form.Group id="password">
                      <Form.Label>Password</Form.Label>
                      <Form.Control type="password" name="password" onChange={this.handleUpdate} required />
                    </Form.Group>
                    <br></br>
                    <Button className="w-100" type="submit" style={{ backgroundColor: 'red', border: '20px #cc0000' }}>Log In</Button>
                  </Form>
                  <br></br>
                  {this.state.error == true && <div className="alert alert-danger" role="alert">
                    Error! Try again
                  </div>}
                </Card.Body>
              </Card>
            </div>
          </main>
        </div>
      </>
    )
  }
}

export default Login
